@import "./Default.scss";

.navBox {
  margin: auto 0;
  display: flex;
  .navBtn {
    @include navBtn;
    // border: 1px solid red;
  }
}

.outter {
  //  border: 1px solid red;
  padding: 0 40px;
  position: fixed;
  bottom: 0;
  top: 100px;
  overflow: scroll;
  .innerBox {
    // display: flex;
    // flex-flow: row wrap;
    .cardBox {
      display: flex;
      flex-flow: row wrap;
      .personCard {
        position: relative;
        max-height: 360px;
        width: 180px;
        margin: 20px;
        .posterBox {
          // transition: all 0.3s ease;
          width: 180px;
          height: 270px;
          overflow: hidden;
          box-shadow: rgba(0, 0, 0, 0.3) 0px 3px 5px 0px;

          img:hover {
            transform: scale(1.08);
            transition: all 0.7s ease;
          }
          img {
            transition: all 0.7s ease;
            width: 100%;
            height: 100%;
          }
        }
        .basicInfo {
          @include centerPosition;
          height: 90px;
          width: 100%;
          font-size: 15px;
          text-align: center;
          color: $color-gray;

          // .titleEn {
          //    margin-bottom: 5px;
          //
          //    line-height: 20px;
          // }

          // .titleZh {
          //    font-size: 13px;
          // }
        }
      }
    }
  }
}
