@import "./Default.scss";
.loginOutter {
  display: flex;
  align-items: center;
  justify-content: center;

  .signInDiv {
    // border: 1px solid red;
    color: $color-blue;
    display: flex;
    text-align: center;
    font-weight: 500;
    @include centerPosition;
    .switchBtn {
      // border-radius: 5px;
      margin: 0 10px;
      display: flex;
      position: relative;
      height: 30px;
      font-size: 0.9rem;
      overflow: hidden;
      box-shadow: rgb(221, 221, 221) 0px 0px 2px 0px inset,
        rgba(219, 219, 219, 0.5) -1px -1px 3px 3px inset;

      div {
        // transition: 0.5s ease;
        @include centerPosition;
        margin: auto 0;
        height: 100%;
        width: 80px;
        cursor: pointer;
      }
      .pressed {
        // transition: 0.3s ease;
        cursor: inherit;
        color: white;
        background-color: $color-blue;
      }
    }
    .userName {
      @include centerPosition;
      width: 150px;
      margin: 10px;
    }
  }
  .loginIcon {
    margin: 10px;
    cursor: pointer;
    width: 35px;
    height: 35px;
    svg {
      fill: $color-blue;
    }
  }
}

.loginDiv {
  @include blackMask;

  span {
    font-size: 20px;
    font-weight: 500;
    color: $color-blue;
  }
  .abc {
    font-size: 10px;
    width: 100%;
  }
  .loginBox {
    position: relative;
    width: 800px;
    height: 500px;
    background-color: white;
    margin: auto;
    flex-direction: column;
    @include centerPosition;

    .inner {
      width: 100%;
      height: 100%;
      .container {
        display: flex;
        width: 100%;
        height: 100%;
        .side {
          width: 30%;
          height: 100%;
          background-color: $color-blue;
          // border: 1px solid red;
          // @include centerPosition;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 50px;
          flex-direction: column;

          .logo {
            width: 120px;
            height: 120px;
          }
          span {
            margin-top: 40px;
            color: white;
          }
        }
        .main {
          width: 70%;
          height: 100%;
          padding: 20px;
          @include centerPosition;
          flex-direction: column;
          .loginBtn {
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px;
            margin: 10px;
            width: 300px;
            padding: 10px 25px;
            display: flex;
            align-items: center;
            cursor: pointer;

            .loginLogo {
              // border: 1px solid blue;
              height: 40px;
              display: flex;
              align-items: center;

              img {
                height: 35px;
              }
            }

            p {
              margin-left: 20px;
              color: $color-gray;
              font-weight: 500;
            }
          }
          .nativeSignIn {
            box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 5px 0px;
            margin: 10px;
            width: 300px;
            padding: 10px 25px;

            form {
              //  display: flex;
              //  flex-direction: column;
              label {
                color: $color-gray;
                font-size: 14px;
              }
              input {
                width: 100%;
                height: 30px;
                outline: 0;
                border-width: 0 0 1px;
                border-color: $color-gray;
                margin-bottom: 10px;
              }
              input:focus {
                border-color: $color-gold;
              }
            }
          }
        }
      }
    }
  }

  .loginClose {
    @include closeBtnStyle;
  }

  .mainBtn {
    background-color: $color-blue;
    color: white;
    height: 40px;
  }
  button {
    width: 100%;
    border: 0;
    //  border-radius: 0.25rem;
    background: none;
    color: $color-blue;
    font-size: 16px;
    line-height: 1.2;
    text-decoration: none;
    // padding: 0.25rem 0.5rem;
    margin: 0.5rem 0;
    cursor: pointer;
  }
  button:focus {
    outline: none;
  }
}
